* {
  line-height: 1.3em;
}

a {
  color: initial;
}

.content_pages {
  padding-top: 52px;

  .content_value {
    min-height: 100vh;
    position: relative;
    display: flex;
    //flex-direction: column;
    justify-content: space-between;
  }

  @media(max-width: 991px) {
    padding-bottom: 52px;
  }
}

.own_icon {
  width: 20px;
  height: 20px;
}

.full_wide {
  width: 100%;
}

.p_main {
  padding: 1em;
}

.transition {
  transition: .3s all;
}

