.records_item {
  flex: 1;

  .item-list-flat-list {
    padding: 0;
    margin-bottom: 10px;

    .medicine-title {
      text-align: left;
    }

    .main-contents {
      padding-left: 0;

      .price {
        text-align: left;
      }

      .stok {
        text-align: left;
      }
    }
  }
}

.schedule_item {
  width: 100%;
  border: 2px solid #e3e3e3;
  padding: 5px;
  margin-bottom: 5px;
  background-color: #e3e3e3;
  border-radius: 5px;

  & .expired {
    background-color: #fda5a5;
  }

  &.warning {
    background-color: #ffff9e;
  }

  &.active {
    background-color: #c2e1c2;
  }

  .schedule_action {
    justify-content: space-between;
    margin-top: 5px;

    .send_icon {
      fill: green !important;
    }

    .active_status {
      padding: 2px 6px;
      border-radius: 3px;
      // color: #e3e3e3;
      font-size: 11px;
      line-height: 1em;
      display: flex;
      justify-content: center;
      align-items: center;
    }


  }

  svg {
    width: 25px !important;
    height: 25px !important;
    opacity: .7;
    fill: #fff !important;
    cursor: pointer;
    margin-left: 10px;

    &:hover {
      opacity: 1;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.records_icon {
  svg {
    width: 25px !important;
    height: 25px !important;
    opacity: .7;
    fill: #fff !important;
    cursor: pointer;
    margin-left: 10px;

    &:hover {
      opacity: 1;
    }
  }
}

.expired_box {
  background-color: #6d6d6d !important;
}