.global_loader {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999999;
  background-color: rgba(0, 0, 0, .2);
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;

  .own_icon {
    fill: #d5417a;
    width: 40px !important;
    height: 40px !important;
    animation: .5s infinite rotation_anim;
  }
}

.box_loader_wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 15px;

  .box_loader {
    width: 100%;
    min-height: 200px;
    background-color: #dd6795;
    opacity: .2;
    margin-bottom: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: 1s infinite box_anim;

    .own_icon {
      fill: #fff;
      width: 40px !important;
      height: 40px !important;
      animation: .5s infinite rotation_anim;
    }
  }
}

@keyframes rotation_anim {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes box_anim {
  0% {
    opacity: .4;
  }

  100% {
    opacity: .2;
  }
}